import teacherApi from '@/api/attendance/teacher'

const state = {
  // 老师主页信息
  teacherAttendance: {},
  // 学生列表信息
  studentAttendance: {},
  // 老师考勤类型
  teacherAttendanceType: {}
}

// getter
const getters = {
  getTeacherAttendance: state => state.teacherAttendance,
  getStudentAttendance: state => state.studentAttendance,
  getTeacherAttendanceType: state => state.teacherAttendanceType
}

// action
const actions = {
  actionTeacherAttendance (store, formData) {
    teacherApi.ClassAttendanceFindtByTeacher(formData, response => {
      store.commit('mutationTeacherAttendance', response.data.data)
    }, response => {
      window.alert(response.data.message)
    })
  },
  actionStudentAttendance (store, formData) {
    teacherApi.StudentAttendanceFindtByClass(formData, response => {
      store.commit('mutationStudentAttendance', response.data.data)
    }, response => {
      window.alert(response.data.message)
    })
  }
}

// mutations
const mutations = {
  mutationTeacherAttendance (state, teacherAttendance) {
    state.teacherAttendance = teacherAttendance
  },
  mutationStudentAttendance (state, studentAttendance) {
    state.studentAttendance = studentAttendance
  }
}

export default {
  state,
  getters,
  actions,
  mutations
}



