/**
 * pickertime
 * @type {Object}
 */
const state = {
  years: [new Date().getFullYear() - 1, new Date().getFullYear(), new Date().getFullYear() + 1],
  months: [],
  days: []
}

/**
 * 获取对应信息
 * @type {Object}
 */
const getters = {
  getYears: state => state.years,
  getMonths: state => state.months,
  getDays: state => state.days
}

const actions = {}

/**
 * 把数据更新到state中
 * @type {Object}
 */
const mutations = {
  mutationYears (state, years) {
    state.years = years
  },
  mutationMonths (state, months) {
    state.months = months
  },
  mutationDays (state, days) {
    state.days = days
  }
}

export default {
  state,
  getters,
  actions,
  mutations
}



