import Vue from 'vue'
import Vuex from 'vuex'
import teacher from './modules/attendance/teacher'
import principal from './modules/attendance/principal'
import pickertime from './modules/pickertime/pickertime'

Vue.use(Vuex)

export default new Vuex.Store({
  modules: {
    teacher,
    principal,
    pickertime
  }
})



