import Vue from 'vue'

/**
 * 园长考勤API
 */
export default {
  /**
   * 根据学校id查询该学校老师和学生到校情况
   * @param {Object} formData 学校ID schoolId和日期 date
   * @param {Function} scb      成功回调函数
   * @param {Function} ecb      错误回调函数
   */
  SchoolAttendanceFindtByschoolId (formData, scb, ecb) {
    const url = `http://192.168.6.178:8080/base/attendanc/post/schoolAttendance_findtByschoolId/${formData.schoolId}/${formData.date}`
    Vue.http.post(url)
    .then(
      response => scb(response)
    ).catch(
      response => ecb(response)
    )
  },
  /**
   * 查询某个学校员工到校离校情况
   * @param {Object} formData 学校ID schoolId 和日期 date
   * @param {Function} scb      成功回调函数
   * @param {Function} ecb      错误回调函数
   */
  StaffAttendanceFindtBySchool (formData, scb, ecb) {
    const url = `http://192.168.6.178:8080/base/attendanc/post/staffAttendance_findtBySchool/${formData.schoolId}/${formData.date}`
    Vue.http.post(url)
    .then(
      response => scb(response)
    ).catch(
      response => ecb(response)
    )
  },
  /**
   * 查询某个学校所有学生到校离校情况
   * @param {Object} formData 学校ID schoolId和日期  date
   * @param {Function} scb      成功回调函数
   * @param {Function} ecb      错误回调函数
   */
  StudentAttendanceFindtBySchool (formData, scb, ecb) {
    const url = `http://192.168.6.178:8080/base/attendanc/post/studentAttendance_findtBySchool/${formData.schoolId}/${formData.date}`
    Vue.http.post(url)
    .then(
      response => scb(response)
    ).catch(
      response => ecb(response)
    )
  }
}



