// SUPER_ADMINISTRATOR = 1;    //超级管理员;
// ORG_ADMINISTRATOR = 2;      //组织管理员;
// SCHOOL_ADMINISTRATOR = 3;   //学校管理员;
// PARENT = 4;                 //家长;
// TEACHER = 5;                //老师;
// PRINCIPAL = 6;              //园长;
// ASSISTANT = 7;              //行政;
// DOCTOR = 8;                 //医生;
// SERVICE = 9;                //后勤;
import header from '@/components/header';
export default {
  data() {
    return {
      roleList: [],
      titleContent: '选择角色'
    }
  },
  created() {
    // 设置角色列表进入对应角色主页
    this.classMap = ['/super','/org','/school','/parent','/teacher','/principal','/assistant','/doctor','/service'];
    //获取角色列表
    this.$http.post(`${WX_URL}/base/login/post/getRoleSelect`,{'mobile': '13699009629','openid': openid}).then((response) => {
      console.log(response);
      response = response.data
      if(response.code && response.code < 2000){
        //赋值数据
        this.roleList = response.data;
        //若用户只有一种身份，则根据用户的类型进入相应的角色主页
        if(this.roleList.length === 1){
          localStorage.setItem('USER', JSON.stringify(this.roleList[0]));
          this.goToIndex(this.roleList[0].roleType);
        }
      }else if (response.code > 2000) {
        //用户数据获取失败，跳到绑定页面
        this.$router.push('/bind');
      }
    });
  },
  methods: {
    //直接跳转到相应的角色主页
    goToIndex(roleType){
      this.$router.push(this.classMap[roleType-1]);
    },
    selectRole(role) {
      localStorage.setItem('USER', JSON.stringify(role));
    }
  },
  components: {
    'v-header':header
  }
}




