import principalApi from '@/api/attendance/principal'

/**
 * 状态
 * @type {Object}
 */
const state = {
  // 园长考勤主页信息
  schoolAttendance: {},
  // 职工列表信息
  staffAttendance: {},
  // 学生列表信息
  studentAttendance: {},
  // 多班级的显示状态
  classesShowContent: [],
  // 初始化teacher-list身份类型
  identity: {
    31: '园长',
    32: '老师',
    33: '医生',
    34: '后勤'
  }
}

/**
 * 获取对应信息
 * @type {Object}
 */
const getters = {
  getSchoolAttendance: state => state.schoolAttendance,
  getStaffAttendance: state => state.staffAttendance,
  getStudentAttendanceBySchool: state => state.studentAttendance,
  getClassesShowContent: state => state.classesShowContent,
  getIdentity: state => state.identity
}

/**
 * 通过API获取数据，并commit
 * @type {Object}
 */
const actions = {
  actionSchoolAttendance (store, formData) {
    principalApi.SchoolAttendanceFindtByschoolId(formData, response => {
      store.commit('mutationSchoolAttendance', response.data.data)
    }, response => {
      window.alert(response.data.message)
    })
  },
  actionStaffAttendance (store, formData) {
    principalApi.StaffAttendanceFindtBySchool(formData, response => {
      store.commit('mutationStaffAttendance', response.data.data)
    }, response => {
      window.alert(response.data.message)
    })
  },
  actionStudentAttendanceBySchool (store, formData) {
    principalApi.StudentAttendanceFindtBySchool(formData, response => {
      store.commit('mutationStudentAttendance', response.data.data)
    }, response => {
      window.alert(response.data.message)
    })
  }
}

/**
 * 把数据更新到state中
 * @type {Object}
 */
const mutations = {
  mutationSchoolAttendance (state, schoolAttendance) {
    state.schoolAttendance = schoolAttendance
  },
  mutationStaffAttendance (state, staffAttendance) {
    state.staffAttendance = staffAttendance
  },
  mutationClassesShowContent (state, classesShowContent) {
    state.classesShowContent = classesShowContent
  },
  mutationStudentAttendance (state, studentAttendance) {
    state.studentAttendance = studentAttendance
  }
}

export default {
  state,
  getters,
  actions,
  mutations
}



