import Vue from 'vue'

/**
 * 老师考勤API
 */
export default {
  /**
   * 根据老师id,时间查找班级应到人数实到人数未到人数
   * @param {Object} formData 职工ID staffId 和 日期date
   * @param {Function} scb      成功回调函数
   * @param {Function} ecb      错误回调函数
   */
  ClassAttendanceFindtByTeacher (formData, scb, ecb) {
    const url = `http://192.168.6.178:8080/base/attendanc/post/classAttendance_findtByTeacher/${formData.staffId}/${formData.date}`
    Vue.http.post(url)
    .then(
      response => scb(response)
    ).catch(
      response => ecb(response)
    )
  },
  /**
   * 老师查询某个班级所有人到校离校情况
   * @param {Object} formData 班级Id classId 和 日期date
   * @param {Function} scb      成功回调函数
   * @param {Function} ecb      错误回调函数
   */
  StudentAttendanceFindtByClass (formData, scb, ecb) {
    const url = `http://192.168.6.178:8080/base/attendanc/post/studentAttendance_findtByClass/${formData.classId}/${formData.date}`
    Vue.http.post(url)
    .then(
      response => scb(response)
    ).catch(
      response => ecb(response)
    )
  }
}



