import Vue from 'vue';
import Router from 'vue-router';

import role from '@/pages/role/role';// 角色选择
Vue.use(Router);

export default new Router({
  // mode: 'history',
  routes: [
    { path: '/', redirect: '/role' },
    { path: '/role', name: 'role', component: role },
    // 角色绑定
    {
      path: '/bind',
      name: 'bind',
      component: () => import('@/pages/bind/bind')
    },
    // 手机验证
    {
      path: '/verify',
      name: 'verify',
      component: () => import('@/pages/verify/verify')
    },
    // 家长端
    {
      path: '/parent',
      name: 'parent',
      component: () => import('@/pages/parent/parent'),
      children: [
        {
          path: '/parent/',
          redirect: 'parentIndex'
        },
        {
          path: '/parent/parentIndex',
          name: 'parentIndex',
          component: () => import('@/pages/parent/index')
        },
        {
          path: '/parent/report',
          name: 'report',
          component: () => import('@/pages/parent/report')
        },
        {
          path: '/parent/attendance',
          name: 'ParentAttendance',
          component: () => import('@/pages/parent/attendance/index'),
          children: [
            {
              path: '',
              name: 'ParentAttendanceHome',
              component: () => import('@/pages/parent/attendance/attendance')
            }
          ]
        }
      ]
    },
    // 园长端
    {
      path: '/principal',
      name: 'principal',
      component: () => import('@/pages/principal/principal'),
      children: [
        {
          path: '/principal/',
          redirect: 'principalIndex'
        },
        {
          path: '/principal/principalIndex',
          name: 'principalIndex',
          component: () => import('@/pages/principal/index')
        },
        {
          path: '/principal/attendance',
          name: 'PrincipalAttendance',
          component: () => import('@/pages/principal/attendance/index'),
          children: [
            {
              path: '',
              name: 'PrincipalAttendanceHome',
              component: () => import('@/pages/principal/attendance/attendance')
            },
            {
              path: 'studentList',
              name: 'PrincipalStudentList',
              component: () => import('@/pages/principal/attendance/studentList'),
            },
            {
              path: 'teacherList',
              name: 'PrincipalTeacherList',
              component: () => import('@/pages/principal/attendance/teacherList'),
            },
            {
              path: 'teacherMonthDetail',
              name: 'PrincipalTeacherMonthDetail',
              component: () => import('@/pages/principal/attendance/teacherMonthDetail'),
            },
            {
              path: 'teacherMonthOtherDetail',
              name: 'PrincipalTeacherMonthOtherDetail',
              component: () => import('@/pages/principal/attendance/teacherMonthOtherDetail'),
            },
            {
              path: 'studentMonthDetail',
              name: 'PrincipalStudentMonthDetail',
              component: () => import('@/pages/principal/attendance/studentMonthDetail'),
            },
            {
              path: 'studentOtherList',
              name: 'PrincipalStudentOtherList',
              component: () => import('@/pages/principal/attendance/studentOtherList'),
            },
            {
              path: 'leaveDetail',
              name: 'PrincipalLeaveDetail',
              component: () => import('@/pages/principal/attendance/leaveDetail'),
            }
          ]
        }
      ]
    },
    // 老师端
    {
      path: '/teacher',
      name: 'teacher',
      component: () => import('@/pages/teacher/teacher'),
      children: [
        {
          path: '/teacher/',
          redirect: 'teacherIndex'
        },
        {
          path: '/teacher/teacherIndex',
          name: 'teacherIndex',
          component: () => import('@/pages/teacher/index')
        },
        ,
        {
          path: '/teacher/attendance',
          name: 'TeacherAttendance',
          component: () => import('@/pages/teacher/attendance/index'),
          children: [
            {
              path: '',
              name: 'TeacherAttendanceHome',
              component: () => import('@/pages/teacher/attendance/attendance')
            },
            {
              path: 'studentList',
              name: 'TeacherStudentList',
              component: () => import('@/pages/teacher/attendance/studentList'),
            },
            {
              path: 'monthDetail',
              name: 'TeacherStudentMonthDetail',
              component: () => import('@/pages/teacher/attendance/monthDetail'),
            },
            {
              path: 'teacherLeave',
              name: 'TeacherLeave',
              component: () => import('@/pages/teacher/attendance/teacherLeave'),
            },
            {
              path: 'leaveDetail',
              name: 'TeacherLeaveDetail',
              component: () => import('@/pages/teacher/attendance/leaveDetail'),
            }
          ]
        }
      ]
    }
  ]
});



